								temp_val.XF = XFs[temp_val.ixfe];
								if(options.cellFormula) temp_val.f = "="+stringify_formula(val.formula,range,val.cell,supbooks, opts);
								if(temp_val.XF) safe_format_xf(temp_val, options, wb.opts.Date1904);
